.header__link {
    color: #fff;
    text-decoration: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    transition: opacity 0.2s ease;
}

.header__link:hover {
    opacity: 0.6;
}

@media screen and (max-width: 767px) {
    .header__link{
        font-size: 14px;
        line-height: 17px;
    }
}