.popup__container {
    max-width: 358px;
    width: 100%;
    background-color: #fff;
    border-radius: 10px;
    position: fixed;
    padding: 34px 36px 37px;
    display: flex;
    flex-direction: column;
}
@media screen and (max-width: 767px) {
    .popup__container{
        max-width: 238px;
        padding: 25px 22px; 
    }
}